.modal-component {
  width: 49%;
  margin: 0 auto;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  height: 200px;
  position: absolute;
  top: 25%;
  left: 25.4%;
  z-index: 5;
}

.modal-component__color {
  background-color: #eb5353;
  padding: 1rem;
  border-radius: 1rem 1rem 0 0;
  font-size: 1.5rem;
}
.modal-component__message {
  background-color: #f8ecd1;
  height: 60%;
  border-radius: 0 0 1rem 1rem;
  padding: 1rem;
}
.modal-component__button {
  width: 5rem;
  height: 3rem;
  background-color: #eb5353;
  position: absolute;
  top: 70%;
  left: 80%;
}
